@use "global" as *;

.voice-card {
  display: block;
  background-color: #fff;
  box-shadow: rem(2) rem(2) rem(10) rgba(0, 0, 0, 0.25);
  cursor: pointer;
  font-family: $font-family_jp1;

  @include mq(md) {
    width: calc((100% / 2) - 3.70370%); //40 / 540*2+40
    max-width: rem(520);
  }
}

.voice-card:hover {
  opacity: 0.7;
}

.voice-card__inner {
  padding: rem(18.8) rem(16) rem(20);

  @include mq(md) {
    padding: rem(18.6) rem(24) rem(20) rem(23);
  }
}

.voice-card__header {
  display: flex;
}

.voice-card__content {
  border-bottom: solid rem(1) $accent;
  width: 47.6038%; /* 149/345-(16*2) */

  @include mq(md) {
    width: 58.4746%; /* 276/520-(24*2) */
  }
}

.voice-card__meta {
  @include mq(md) {
    display: flex;
    justify-content: space-between;
    align-items: end;
    margin-top: rem(30);
  }
}

.voice-card__gender {
  color: $text-base;
  font-size: rem(12);
  font-weight: $regular;
  line-height: 1.3;
  letter-spacing: rem(-0.5);

  @include mq(md) {
    line-height: 1.2;
  }
}

.voice-card__category {
  display: inline-block;
  padding: rem(4.2) rem(11);
  color: $accent;
  font-size: rem(12);
  font-weight: $regular;
  line-height: 1.8333;
  border: solid rem(1) $accent;

  @include mq(md) {
    padding: rem(4.2) rem(15) rem(1);
    line-height: 2.2;
  }
}

.voice-card__title {
  color: $text-base;
  font-size: rem(16);
  font-weight: $normal;
  line-height: 1.4375; /* 1.5; カンプに値がない、コンテンツの高さが69、3行あるから/3で23、fzが16なので23/16 */
  margin-top: rem(6);

  @include mq(md) {
    font-size: rem(24);
    margin-top: rem(12.64);
  }
}

.voice-card__img {
  width: 48.2428%;
  position: relative;
  margin-left: rem(13);
  margin-top: rem(6);

  @include mq(md) {
    margin-left: rem(16);
    width: 38.1356%; /* 180/520-(24*2) */
  }
}

.voice-card__img img {
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.voice-card__body {
  margin-top: rem(20);

  @include mq(md) {
    margin-top: rem(43);
  }
}

.voice-card__text {
  font-size: rem(16);
  line-height: 1.5;
  font-weight: $regular;
  color: $text-base; //要確認カンプでは黒になってる
  letter-spacing: rem(-0.16);

  @include mq(md) {
    letter-spacing: rem(-0.09);
  }
}
